import web3 from '../utils/web3'
import nftAbi from '../abi/nft.json'
import config from '@/config/app.config.js' // 引入配置文件

const contractInstance = new web3.web3.eth.Contract(nftAbi, config.KOL)

/**
 *
 * @param {tokenId} nftId
 * @returns
 */
const getApproved = async (tokenId) => {
  console.log('tokenId', tokenId)
  try {
    const resp = await contractInstance.methods.getApproved(tokenId).call({})
    return web3.successResult(resp)
  } catch (error) {
    return web3.failResult(error)
  }
}

/**
 *
 * @param {tokenAddress} 合约地址
 * @param {tokenId} nftId
 * @returns
 */
const approve = async (tokenId, tokenAddress = config.kolRights) => {
  try {
    const accounts = await web3.web3.eth.getAccounts()
    const resp = await contractInstance.methods
      .approve(tokenAddress, tokenId)
      .send({
        from: accounts[0]
      })
    return web3.successResult(resp)
  } catch (error) {
    return web3.failResult(error)
  }
}

/**
 *
 * @param {tokenAddress} ka合约地址
 * @param {from} 持有nft地址
 * @param {to} 接受nft地址
 * @param {tokenId} nftId
 * @returns
 */
const transferFrom = async (toAddress, tokenId, tokenAddress = config.SUT) => {
  const contractToken = new web3.web3.eth.Contract(nftAbi, tokenAddress)
  try {
    const accounts = await web3.web3.eth.getAccounts()
    const resp = await contractToken.methods
      .safeTransferFrom(accounts[0], toAddress, tokenId)
      .send({
        from: accounts[0]
      })
    return web3.successResult(resp)
  } catch (error) {
    return web3.failResult(error)
  }
}

const getName = async (type) => {
  var tokenAddress
  switch (type) {
    case 1:
      tokenAddress = config.KOL
      break
    case 2:
      tokenAddress = config.SPT
      break
    case 3:
      tokenAddress = config.SMT
      break
    default:
      tokenAddress = config.SUT
      break
  }

  const contractToken = new web3.web3.eth.Contract(nftAbi, tokenAddress)
  try {
    const resp = await contractToken.methods
      .name()
      .call({})
    return web3.successResult(resp)
  } catch (error) {
    return web3.failResult(error)
  }
}
const tokenObj = {
  getApproved,
  approve,
  transferFrom,
  getName
}

export default tokenObj
