import request from '@/utils/request'

// 预售信息
/***
 * type 1 kol
 */
export const queryPreSaleInfo = (params) => {
  return request('GET', '/dapp/preSale/index', params)
}

// kol预售-mint - 填写特邀码
export const queryMintKol = (code) => {
  const params = {
    code: code
  }
  return request('GET', '/dapp/preSale/setPurchaseCode', params, true, true)
}
